import React from 'react'
import {Link} from 'react-router-dom'

const Navbar = () => {
    return (
        <nav className='teal'>
            <div className="nav-wrapper">
                <Link to="/" className="brand-logo center hide-on-med-and-down">Recipes`Finder</Link>
                <Link to="/" className="brand-logo left hide-on-large-only">Recipes`Finder</Link>
                <ul className="right">
                    <li className='hide-on-med-and-down'><Link to="/favorite"><i className="material-icons left">favorite</i>Favorite</Link></li>
                    <li className='hide-on-med-and-down'><Link to="/coockinglist"><i className="material-icons right">format_list_bulleted</i>Cooking list</Link></li>
                    <li className='hide-on-large-only'><Link to="/favorite"><i className="material-icons right">favorite</i></Link></li>
                    <li className='hide-on-large-only'><Link to="/coockinglist"><i className="material-icons right">format_list_bulleted</i></Link></li>
                </ul>        
            </div>
        </nav>
        
    )
}

export default Navbar