import React from 'react'

import {Link} from 'react-router-dom'
import { connect } from 'react-redux'
import { deleteFavorite } from '../actions/recipeActions'

const Favorite = (props) => {
    return(
        <div className='container'>
            <div className='row'>
                {props.favorite.map((recipe) => {
                    return(
                        <div key={recipe.recipe_id} className='col s12 l4'>
                           <div className='card z-depth-2'>
                               <div className='card-image'>
                                    <img 
                                    class="materialboxed" 
                                    height="250" 
                                    src={recipe.image_url} 
                                    alt={recipe.title}/>
                               </div>
                               <div className='card-content'>
                                   <span className='card-title '>{ 
                                       recipe.title.length < 20 ? `${recipe.title}` : `${recipe.title.substring(0,20)}`
                                       }
                                    </span>
                                   <p>Publisher: {recipe.publisher}</p>
                               </div>
                               <div className='card-action teal'>
                                    <Link className='white-text' to={{
                                        pathname: `/recipe/${recipe.recipe_id}`,
                                        state: { recipe: recipe.recipe_id }
                                        }}>View Recipe</Link>
                                    <Link 
                                    className='white-text right' 
                                    to='#' 
                                    onClick={()=>(props.deleteFavorite(recipe.recipe_id))}>Delete</Link>
                                </div>
                            </div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}

const mapStateToProps = state =>({
    favorite: state.favorite.items
});

export default connect(mapStateToProps,{deleteFavorite})(Favorite)