import { ADD_TO_FAVORITE, DELETE_FAVORITE } from '../actions/type'

const initialState = {
    items: []
}

export default function (state = initialState, action) {
    switch(action.type) {
        case ADD_TO_FAVORITE:
            return {
                ...state,
                 items: state.items.concat(action.payload)
            }
        case DELETE_FAVORITE:
            return {items: state.items.filter(({recipe_id} )=> recipe_id !== action.payload)}
            
        default: 
            return state;
    }
}