import React,{Component} from 'react'
import {Link} from 'react-router-dom'
import { activeFetchRecipe } from '../actions/recipeActions'
import { connect } from 'react-redux'

var API_KEY = '2354f02ad293b30e585c026f344dd7c5'

class Recipe extends Component {
    componentDidMount = async () => {
        var RANDOM = Math.floor(Math.random() * (2 - 1 + 1)) + 1
        if (RANDOM === 1){
            API_KEY = '2354f02ad293b30e585c026f344dd7c5'
        } else{ 
            if (RANDOM === 2){
                API_KEY = '36f4cf79aa531df6d70f839602b27a39'
            }
        }
        const id = this.props.location.state.recipe;
        this.props.activeFetchRecipe(API_KEY, id)
    }
    render() {
        if (this.props.loading === false) {
            if(this.props.activeRecipe) {
                return (
                    <div>
                        <div className='container' key={this.props.activeRecipe.recipe_id}>
                            <div className='row' >
                                <div className='col l5 s12'>
                                    <div className='card z-depth-2'>
                                        <div className='card-image'>
                                            <img 
                                            className="materialboxed" 
                                            src={this.props.activeRecipe.image_url} 
                                            height="250" 
                                            width="350"
                                            alt={this.props.activeRecipe.title}/>
                                        </div>
                                        <div className='card-content'>
                                            <span className='card-title '>Publisher: {this.props.activeRecipe.publisher}</span>
                                        </div>
                                        <div className='card-action teal'>
                                            <a className='white-text' 
                                            href={`${this.props.activeRecipe.source_url}`} 
                                            target="_blank" 
                                            rel="noopener noreferrer">Go to {this.props.activeRecipe.publisher}</a>
                                            <Link className='white-text right hide-on-large-only' to='/'>Go Main</Link>
                                            <Link className='white-text right hide-on-med-and-down' to='/'>Go Main</Link>
                                        </div>
                                    </div>
                                </div>
                                <div className ='col s12 l7'>
                                    <ul class="collection with-header">
                                        <li class="collection-header"><h4>{ this.props.activeRecipe.title}</h4></li>
                                        {this.props.activeRecipe.ingredients.map((item,index) =>{
                                            return(
                                                <li class="collection-item">{index + 1}. {item}</li>
                                            )
                                        })}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                )
            } else {
                return(
                    <div>
                        <div className='container'>
                            <h5 className='col s12 center teal-text'>Sorry, the recipe has been deleted.....</h5>
                        </div>
                    </div>
                )
            }
        }
        else {
            return(
                <div className='container'>
                    <div className='spin'>
                        <div className='col s6 center'>
                            <div className="preloader-wrapper big active">
                                <div className="spinner-layer spinner-teal-only">
                                    <div className="circle-clipper left">
                                        <div className="circle"></div>
                                    </div><div className="gap-patch">
                                        <div className="circle"></div>
                                    </div><div className="circle-clipper right">
                                        <div className="circle"></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        
    }
}

const mapStateToProps = state =>({
    activeRecipe: state.activerecipe.items.recipe,
    loading: state.activerecipe.loading
});

export default connect(mapStateToProps,{activeFetchRecipe})(Recipe)




