import React from 'react'

import { connect } from 'react-redux'
import { fetchRecipe } from '../../actions/recipeActions'

var API_KEY = '10cda71c654d77a2b8cb665666bafdf7'



const Form = (props) => {
    const getRecipe = async (e) => {
        const recipeName = e.target.elements.recipeName;
        var RANDOM = Math.floor(Math.random() * (4 - 1 + 1)) + 1
        if (RANDOM === 1){
            API_KEY = '10cda71c654d77a2b8cb665666bafdf7'
        } else{ 
            if (RANDOM === 2){
                API_KEY = '36f4cf79aa531df6d70f839602b27a39'
            }else{
                if(RANDOM ===3){
                    API_KEY = '287cbf43143dba501c5335026eac53de'
                }else{
                    if(RANDOM === 4){
                        API_KEY = '1e017694c1dd5a0eb1a0c42ce2a2372e'
            }}
        }}
        e.preventDefault();
        props.fetchRecipe(API_KEY, recipeName.value)
        recipeName.value =''
    }
    return(
        <div className='container'>
            <form className='col s12' onSubmit={getRecipe}>
                <div className='row'>
                    <div className='input-field col s10 offset-s1'>
                        <input name='recipeName' id="input_text" type="text" data-length="40"/>
                        <label for="input_text">Find what you like...</label>
                    </div>
                </div>
            </form>   
        </div>
    )
}

export default connect(null,{fetchRecipe})(Form)