import React from 'react'

import { connect } from 'react-redux'
import { addToItem } from '../../actions/recipeActions'

const ListForm = (props) => {
    const handleSubmit = (e) => {
        e.preventDefault();
        const ingridientsName = e.target.elements.ingridientsName;
        const grams = e.target.elements.grams
        const id = Math.random()
        if ((grams.value && ingridientsName.value) || ingridientsName.value) {
            props.addToItem({ingridientsName: ingridientsName.value ,grams: grams.value,id: id,active: false})

        }
        ingridientsName.value = ''
        grams.value = ''
    }
    return(
        <div className='container'>
            <form className='col s12' onSubmit={handleSubmit}>
                <div className='row'>
                    <div className='input-field col s6 '>
                        <input name='ingridientsName' id="input_ingridient" type="text" data-length="100"/>
                            <label for="input_ingridient">Ingredients...</label>
                    </div>
                    <div className='input-field col s3 offset-s1'>
                    <input name='grams' id="input_grams" type="text" data-length="10"/>
                        <label for="input_grams">Amount...</label>
                    </div>
                    <div className='center col s12'>
                        <button className="btn waves-effect waves-light" type="submit" name="action">Submit
                            <i className="material-icons right">send</i>
                        </button>
                    </div>
                </div>
            </form>   
        </div>
    )
}

export default connect(null,{addToItem})(ListForm)