import React from 'react'

import {connect} from 'react-redux'
import { deleteItem , toggleItem} from '../../actions/recipeActions'

const List = (props) => {

    return(
        <div className='container'>
            <ul className="collection with-header">
                 <li className="collection-header center"><h4>Ingridients</h4></li>
                {props.list.map((item, index) => {
                    var active
                    if (item.active === true){
                        active = 'collection-item active'
                    } else {
                        active = 'collection-item'
                    }
                    return(
                        <li className={active} key={item.id}>
                            <div className='flow-text' >{item.ingridientsName}
                                <a href="#!" className="secondary-content" onClick={()=>(props.toggleItem(index))}>
                                    <i className="small material-icons"> done</i></a>
                                <a href="#!" className="secondary-content" onClick={()=>(props.deleteItem(item.id))}>
                                    <i className="small material-icons">delete_forever</i></a>
                                <a href="#!" onClick={()=>(props.toggleItem(item.id))} className="secondary-content flow-text ">{item.grams}
                                </a>
                            </div>
                        </li>
                    )
                })}
            </ul>
        </div>
    )
}
const mapStateToProps = state =>({
    list: state.list.items
});


export default connect(mapStateToProps,{deleteItem, toggleItem})(List)