import { ACTIVE_FETCH_RECIPES, ACTIVE_FETCH_LOADING } from '../actions/type'

const initialState = {
    items: {},
    loading: false
}

export default function (state = initialState, action) {
    switch(action.type) {
        case ACTIVE_FETCH_LOADING:
        return {
            ...state,
            loading: true
        }
        case ACTIVE_FETCH_RECIPES:
            return {
                ...state,
                items: action.payload,
                loading: false
            }
        default: 
            return state;
    }
}