import { ADD_ITEM, DELETE_ITEM, TOGGLE_ITEM } from '../actions/type'

const initialState = {
    items: []
}

export default function (state = initialState, action) {
    switch(action.type) {
        case ADD_ITEM:
            return {
                ...state,
                 items: state.items.concat(action.payload)
            }
        case DELETE_ITEM:
            return {items: state.items.filter(({id} )=> id !== action.payload)
        }
        case TOGGLE_ITEM:
            console.log(action.payload)
            let arr = [...state.items]
            arr[action.payload] = {...arr[action.payload], 'active': !arr[action.payload]['active']}
            return {
                ...state,
                items: arr
            }
            
        default: 
            return state;
    }
}