import React from 'react'
import {Link} from 'react-router-dom'
import { connect } from 'react-redux'
import { addToFavorite } from '../../actions/recipeActions'
import M from 'materialize-css'


const Recipe = (props) => {
    if (props.recipes) {
        return(
            <div className='container'>
                <div className='row'>
                {props.recipes.map((recipe) => {
                        return(
                            <div key={recipe.recipe_id} className='col s12 l4'>
                            <div className='card z-depth-2'>
                                <div className='card-image'>
                                        <img 
                                        class="materialboxed" 
                                        height="250" 
                                        src={recipe.image_url} 
                                        alt={recipe.title}/>
                                        <Link to='#' id='toast' onClick={()=>(props.addToFavorite(recipe),M.toast({html: 'Added to Favorite', classes: 'teal'}))} className='halfway-fab btn-floating teal pulse'>
                                            <i className='material-icons'>favorite</i>
                                        </Link>
                                </div>
                                <div className='card-content'>
                                    <span className='card-title '>{ recipe.title.length < 20 ? `${recipe.title}` : `${recipe.title.substring(0,20)}`}</span>
                                    <p>Publisher: {recipe.publisher}</p>
                                </div>
                                <div className='card-action teal'>
                                        <Link className='white-text' to={{
                                            pathname: `/recipe/${recipe.recipe_id}`,
                                            state: { recipe: recipe.recipe_id }
                                            }}>View Recipe</Link>
                                    </div>
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>
        )
    }else {return(<div><h5>Ops......</h5></div>)}
}  
const mapStateToProps = state =>({
    recipes: state.recipes.items
});

export default connect(mapStateToProps,{addToFavorite})(Recipe)