import { FETCH_RECIPES, FETCH_LOADING, ACTIVE_FETCH_RECIPES, ADD_TO_FAVORITE, DELETE_FAVORITE, ADD_ITEM, DELETE_ITEM, TOGGLE_ITEM, ACTIVE_FETCH_LOADING} from './type'

export const fetchRecipe = (API_KEY, recipeName) => dispatch => {
    dispatch({type: FETCH_LOADING, payload:null})
    fetch(`https://cors-anywhere.herokuapp.com/http://food2fork.com/api/search?key=${API_KEY}&q=${recipeName}&count=12`)
        .then(res => res.json())
        .then(data => 
            dispatch({
                type:FETCH_RECIPES,
                payload: data.recipes
            })
        )  
}

export const activeFetchRecipe = (API_KEY, id) => dispatch => {
    dispatch({type: ACTIVE_FETCH_LOADING, payload:null})
    fetch(`https://cors-anywhere.herokuapp.com/https://www.food2fork.com/api/get?key=${API_KEY}&rId=${id}`)
        .then(res => res.json())
        .then(data => 
            dispatch({
                type:ACTIVE_FETCH_RECIPES,
                payload: data
            })
        )  
}

export const addToFavorite = (favorite) => dispatch => {
    dispatch({
        type:ADD_TO_FAVORITE,
        payload: favorite
    })
}

export const deleteFavorite = ( id ) => dispatch => {
    dispatch({
        type:DELETE_FAVORITE,
        payload: id
    })
}

export const addToItem = (item) => dispatch => {
    dispatch({
        type:ADD_ITEM,
        payload: item
    })
}

export const deleteItem = ( id ) => dispatch => {
    dispatch({
        type:DELETE_ITEM,
        payload: id
    })
}

export const  toggleItem = (index) => dispatch => {
    dispatch({
        type:TOGGLE_ITEM,
        payload: index
    })
}