import { FETCH_RECIPES, FETCH_LOADING } from '../actions/type'

const initialState = {
    items: [],
    loading: false
}

export default function (state = initialState, action) {
    switch(action.type) {
        case FETCH_LOADING:
            return {
                ...state,
                loading: true
            }
        case FETCH_RECIPES:
            return {
                ...state,
                items: action.payload,
                loading: false
            }
        default: 
            return state;
    }
}